:root {
  --color-primary-light: #e67e22;
  --color-primary-dark: #e1b12c;
  --color-t-light: #f5f6fa;
  --color-t-light-2: #c6c6ca;
  --color-t-light-3: #fff;
  --color-t-dark: #1e272e;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  background: var(--color-t-light);
}

.container {
  max-width: 140rem;
  margin: 0 auto;
}

body {
  font-size: 1.6rem;
  color: var(--color-typography-dark);
  font-family: "Open Sans", sans-serif;
}

.u-margin-top-small {
  margin-top: 1rem;
}

.u-margin-top-medium {
  margin-top: 2rem;
}

.u-margin-top-big {
  margin-top: 4rem;
}

.footer {
  text-align: center;
  margin-top: 3rem;
  padding: 3rem 0;
  background: var(--color-t-dark);
  color: var(--color-t-light);
}
@media only screen and (max-width: 26em) {
  .footer {
    font-size: 1.4rem;
  }
}
.footer a {
  color: var(--color-t-light);
  text-decoration: none;
}
.footer .social-links {
  padding-bottom: 2rem;
  border-bottom: 1px solid #3a434a;
}
@media only screen and (max-width: 26em) {
  .footer .social-links {
    display: flex;
    flex: 0 50%;
    justify-content: space-around;
    font-size: 1.4rem;
  }
}
.footer .social-links a {
  margin-right: 2rem;
}
@media only screen and (max-width: 26em) {
  .footer .social-links a {
    margin: 0;
  }
}
.footer .social-links .twitter:hover {
  transition: all 0.2s ease-out;
  color: #00acee;
}
.footer .social-links .fb:hover {
  transition: all 0.2s ease-out;
  color: #3b5998;
}
.footer .social-links .ig:hover {
  transition: all 0.2s ease-out;
  color: #c13584;
}
.footer .social-links .yt:hover {
  transition: all 0.2s ease-out;
  color: #c4302b;
}
.footer .row {
  font-weight: 300;
  margin-top: 3rem;
}
@media only screen and (max-width: 26em) {
  .footer .row {
    display: none;
  }
}
.footer .row a {
  margin-right: 2rem;
}
@media only screen and (max-width: 26em) {
  .footer .row a {
    display: flex;
    flex: 0 20%;
  }
}
.footer .row a:hover {
  text-decoration: underline;
}
.footer .poweredBy {
  margin-top: 2rem;
  padding-bottom: 3rem;
  border-bottom: 1px solid #3a434a;
}
.footer .poweredBy a:hover {
  text-decoration: underline;
}
.footer .copyright {
  margin-top: 2rem;
}

.navigation {
  background: var(--color-primary-dark);
  position: relative;
}
.navigation .container {
  display: flex;
  height: 5rem;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 3rem;
}
@media only screen and (max-width: 26em) {
  .navigation .container {
    flex-direction: column;
    height: auto;
  }
}
.navigation .container .navigation-logo {
  font-family: "Dancing Script", cursive;
}
@media only screen and (max-width: 26em) {
  .navigation .container .navigation-logo {
    width: 100%;
    justify-content: flex-start;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.navigation .container .navigation-logo .bars {
  display: none;
}
@media only screen and (max-width: 26em) {
  .navigation .container .navigation-logo .bars {
    display: flex;
    font-size: 2rem;
    color: var(--color-t-light);
  }
}
.navigation .container .navigation-logo h1 {
  font-size: 2.5rem;
  color: var(--color-t-light);
}
.navigation .container .list {
  display: flex;
  height: 100%;
  align-items: center;
}
@media only screen and (max-width: 26em) {
  .navigation .container .list {
    flex-direction: column;
    margin-top: 1rem;
    width: 100%;
  }
}
.navigation .container .list .active {
  background: var(--color-t-light);
  color: var(--color-primary-dark);
}
.navigation .container .list-item {
  list-style: none;
  height: 100%;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 26em) {
  .navigation .container .list-item {
    width: 100%;
  }
}
.navigation .container .list-item:last-child {
  border-right: 1px solid var(--color-t-light);
}
@media only screen and (max-width: 26em) {
  .navigation .container .list-item:last-child {
    border: none;
  }
}
.navigation .container .list-item-link {
  display: flex;
  align-items: center;
  height: 100%;
  text-decoration: none;
  padding: 0 1rem;
  color: var(--color-t-light);
  border-left: 1px solid var(--color-t-light);
  width: 12rem;
  justify-content: center;
}
@media only screen and (max-width: 26em) {
  .navigation .container .list-item-link {
    border: none;
    padding: 1rem;
    width: 100%;
    justify-content: flex-start;
  }
}
.navigation .container .list-item-link:hover {
  transition: all 0.2s ease-out;
  background: var(--color-t-light);
  color: var(--color-primary-dark);
}
@media only screen and (max-width: 26em) {
  .navigation .container .navigation-search {
    display: none;
  }
}
.navigation .container .navigation-search-input {
  padding: 1rem;
  width: 30rem;
  border: none;
  background: transparent;
  border-bottom: 1px solid var(--color-t-light);
  color: var(--color-t-light);
}
.navigation .container .navigation-search-input::placeholder {
  color: var(--color-t-light);
}
.navigation .container .navigation-search-input:focus {
  outline: none;
}

.section-main {
  display: flex;
}
@media only screen and (max-width: 26em) {
  .section-main {
    flex-direction: column;
    width: 100%;
  }
}
.section-main-left {
  flex: 0 70%;
  /* display: flex;
  flex-wrap: wrap; */
  border-right: 1px solid var(--color-t-light-2);
}
@media only screen and (max-width: 26em) {
  .section-main-left {
    border: none;
    flex: 0 100%;
  }
}
.section-main-left .league-description {
  padding-right: 2rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .league-description {
    padding: 1rem;
  }
}
.section-main-left .league-description h1 {
  font-weight: 400;
  margin-bottom: 2rem;
}
.section-main-left .league-description p a {
  text-decoration: none;
  color: var(--color-primary-dark);
}
.section-main-left .league-description p a:hover {
  text-decoration: underline;
}
.section-main-left .league-description span {
  font-weight: 600;
}
.section-main-left .infos {
  padding: 2rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos {
    padding: 1rem;
    width: 100%;
  }
}
.section-main-left .infos .past-clubs {
  display: flex;
  flex-direction: column;
}
.section-main-left .infos .past-clubs h1 {
  text-align: center;
  margin-top: 3rem;
}
.section-main-left .infos .past-clubs .past-club {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .past-clubs .past-club .image {
    flex: 0 20%;
  }
}
.section-main-left .infos .past-clubs .past-club .image img {
  width: 100%;
  height: 4rem;
  object-fit: scale-down;
}
.section-main-left .infos .past-clubs .past-club p {
  font-weight: 400;
}
.section-main-left .infos .schedule {
  margin-bottom: 7rem;
}
.section-main-left .infos .schedule h1 {
  font-weight: 400;
  text-align: center;
  margin-top: 3rem;
}
.section-main-left .infos .schedule .sides {
  display: flex;
  margin-top: 3rem;
}
.section-main-left .infos .schedule .sides h1 {
  text-align: left;
  padding: 1rem;
  font-size: 1.8rem;
}
.section-main-left .infos .schedule .sides .last-5 {
  flex: 0 50%;
}
.section-main-left .infos .schedule .sides .next-5 {
  flex: 0 50%;
}
.section-main-left .infos .schedule .sides .result {
  padding: 1rem;
  border: 1px solid var(--color-t-light-2);
  margin: 0.5rem;
}
.section-main-left .infos .schedule .sides .result p:first-child {
  font-weight: 600;
}
.section-main-left .infos .roster h1 {
  font-weight: 400;
  text-align: center;
  margin-top: 3rem;
}
.section-main-left .infos .roster .grid {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 1rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .roster .grid {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .roster .grid .player .image img {
    height: 5rem;
    object-fit: scale-down;
  }
}
.section-main-left .infos .roster .grid .player .image img {
  width: 100%;
  object-fit: scale-down;
  border-radius: 50%;
}
.section-main-left .infos .roster .grid .player p {
  font-size: 1.3rem;
  text-align: center;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .roster .grid .player p {
    font-size: 1.2rem;
  }
}
.section-main-left .infos .main-part {
  display: flex;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .main-part {
    flex-direction: column;
  }
}
.section-main-left .infos .main-part .image {
  flex: 0 30%;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .main-part .image {
    flex: 0 100%;
  }
}
.section-main-left .infos .main-part .image img {
  width: 100%;
  object-fit: scale-down;
  height: 20rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .main-part .image img {
    height: 10rem;
    object-fit: scale-down;
  }
}
.section-main-left .infos .main-part .desc {
  flex: 0 70%;
  margin-left: 1rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .infos .main-part .desc {
    flex: 0 100%;
    margin-left: 0;
  }
}
.section-main-left .infos .main-part .desc .social-links {
  margin-top: 2rem;
  text-decoration: none;
  font-size: 2rem;
}
.section-main-left .infos .main-part .desc .social-links a {
  margin-right: 1rem;
}
.section-main-left .infos .main-part .desc .social-links .fb {
  color: #3b5998;
}
.section-main-left .infos .main-part .desc .social-links .ig {
  color: #c13584;
}
.section-main-left .infos .main-part .desc .social-links .tw {
  color: #00acee;
}
.section-main-left .infos .main-part .desc h1 {
  font-weight: 400;
  margin-bottom: 2rem;
}
.section-main-left .infos .main-part .desc h1 span {
  font-size: 1.5rem;
}
.section-main-left .infos .main-part .desc p {
  font-weight: 600;
}
.section-main-left .infos .main-part .desc p span {
  font-weight: 400;
}
.section-main-left .infos .main-part .desc .main-desc {
  font-weight: 400;
  margin-top: 2rem;
}
.section-main-left .cards {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0.5rem;
  padding-right: 2rem;
  margin-top: 1rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .cards {
    padding: 1rem;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 1rem;
  }
}
.section-main-left .cards .card {
  text-decoration: none;
  border: 1px solid var(--color-t-light-2);
}
.section-main-left .cards .card:hover img {
  opacity: 0.9;
}
.section-main-left .cards .card:hover h1 {
  text-decoration: underline;
  color: #78787a;
}
.section-main-left .cards .card h1 {
  font-weight: 400;
  font-size: 1.5rem;
  text-align: center;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  color: var(--color-t-dark);
}
@media only screen and (max-width: 26em) {
  .section-main-left .cards .card h1 {
    font-size: 1.3rem;
  }
}
.section-main-left .cards .card .image img {
  width: 100%;
  object-fit: scale-down;
  height: 10rem;
  margin-top: 0.5rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .cards .card .image img {
    height: 7rem;
  }
}
.section-main-left .articles-grid-news {
  display: flex;
  flex-direction: column;
}
.section-main-left .articles-grid-news .art {
  display: flex;
  border-bottom: 1px solid var(--color-t-light-2);
  padding-bottom: 1rem;
  margin-top: 0.5rem;
  text-decoration: none;
}
@media only screen and (max-width: 26em) {
  .section-main-left .articles-grid-news .art {
    padding: 1rem;
  }
}
@media only screen and (max-width: 26em) {
  .section-main-left .articles-grid-news .art h1 {
    font-size: 1.9rem;
  }
}
.section-main-left .articles-grid-news .art:hover h1 {
  text-decoration: underline;
  color: #78787a;
}
.section-main-left .articles-grid-news .art .image {
  flex: 0 20%;
}
.section-main-left .articles-grid-news .art .image img {
  width: 100%;
  height: 12rem;
  object-fit: cover;
}
.section-main-left .articles-grid-news .art .desc {
  flex: 0 80%;
  margin-left: 2rem;
}
.section-main-left .articles-grid-news .art .desc h1 {
  font-weight: 500;
  font-size: 2.2rem;
  color: var(--color-t-dark);
}
.section-main-left .articles-grid-news .art .desc p {
  font-size: 1.4rem;
  margin-top: 1rem;
  color: var(--color-t-dark);
}
.section-main-left .article-main {
  flex: 0 100%;
  padding: 1rem;
  display: flex;
  cursor: pointer;
  color: var(--color-t-dark);
  text-decoration: none;
}
@media only screen and (max-width: 26em) {
  .section-main-left .article-main {
    flex-direction: column;
    padding: 0.5rem;
  }
}
.section-main-left .article-main:hover h1 {
  text-decoration: underline;
  color: #78787a;
}
.section-main-left .article-main:hover img {
  opacity: 0.9;
}
.section-main-left .article-main-img {
  flex: 0 66%;
}
.section-main-left .article-main-img img {
  width: 100%;
  height: 20rem;
  object-fit: cover;
}
.section-main-left .article-main-desc {
  flex: 0 34%;
  margin-left: 1rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .article-main-desc {
    margin-top: 1rem;
  }
}
.section-main-left .article-main-desc h1 {
  font-size: 2.2rem;
}
.section-main-left .article-main-desc p {
  font-size: 1.4rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .article-main-desc p {
    margin-top: 0.5rem;
  }
}
.section-main-left .article {
  padding: 1rem;
  flex: 0 33%;
  flex-wrap: wrap;
  cursor: pointer;
  color: var(--color-t-dark);
  text-decoration: none;
}
@media only screen and (max-width: 26em) {
  .section-main-left .article {
    flex: 0 50%;
    padding: 0.5rem;
  }
}
.section-main-left .article-img img {
  width: 100%;
  height: 15rem;
  object-fit: cover;
}
.section-main-left .article-desc h1 {
  font-size: 1.7rem;
}
@media only screen and (max-width: 26em) {
  .section-main-left .article-desc h1 {
    font-weight: 600;
  }
}
.section-main-left .article:hover h1 {
  text-decoration: underline;
  color: #78787a;
}
.section-main-left .article:hover img {
  opacity: 0.9;
}
.section-main-right {
  flex: 0 30%;
  padding-left: 3rem;
}
@media only screen and (max-width: 26em) {
  .section-main-right {
    border: none;
    flex: 0 100%;
    margin-top: 2rem;
    text-align: center;
    display: none;
  }
}
.section-main-right h1 {
  font-weight: 400;
}
.section-main-right .events {
  margin-top: 3rem;
}
.section-main-right .events .event {
  margin-bottom: 1.5rem;
}
.section-main-right .events .event:last-child {
  margin-bottom: 3rem;
}
.section-main-right .events .event .small {
  font-size: 1.2rem;
  color: #78787a;
}

.article-news {
  padding: 2rem;
}
@media only screen and (max-width: 26em) {
  .article-news {
    padding: 1rem;
  }
}
.article-news .source {
  border-left: 5px solid var(--color-primary-dark);
  padding-left: 1rem;
  text-transform: uppercase;
  color: var(--color-primary-dark);
  margin-bottom: 1rem;
}
.article-news h1 {
  font-weight: 400;
  font-size: 3rem;
  margin-bottom: 2rem;
}
.article-news .image img {
  width: 100%;
  height: 50rem;
  object-fit: cover;
}
@media only screen and (max-width: 26em) {
  .article-news .image img {
    height: 25rem;
  }
}
.article-news .description {
  margin-top: 1rem;
  font-weight: 600;
  margin-bottom: 2rem;
