}
.article-news .content {
  line-height: 1.5;
}
.article-news .author {
  margin-top: 2rem;
  border-top: 1px solid var(--color-t-light-2);
  padding-top: 1rem;
}
.section-main {
  margin-bottom: 5rem;
}

.section-main-left-newsGridFlex {
  display: flex;
  flex-wrap: wrap;
}

.span-400 {
